<template>
  <div class="UpdateName">

    <el-form
        :model="userFrom"
        status-icon
        :rules="rules"
        ref="userFrom"
        label-width="120px"
    >
      <el-form-item label="当前用户名：" label-width="120px" style="width:40%" >
        <span class="code">{{userInfo.username}}</span>
      </el-form-item>
      <el-form-item label="新用户名：" prop="username" label-width="120px" style="width:40%" >
        <el-input v-model="userFrom.username" placeholder="请输入2~8位用户名" ></el-input>
      </el-form-item>
      <el-form-item label-width="120px">
        <el-button type="primary" @click="submit">确认修改</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import {mapState,mapMutations} from "vuex"

export default {
  name: "ChangeName",
  data(){
    return{
      userFrom: {
        username:"",
      },
      //验证规则
      rules: {
        username: [
          {
            validator: (rule, value, cb) => {

              //长度验证
              if (value.length > 8 || value.length === 1) {
                cb(new Error("请输入2~8位的用户名"))
              }else if (value.length === 0){
                this.$refs['userFrom'].resetFields();
                return;
              }

              //规则验证
              if(!/^[\w\u4e00-\u9fa5\u0800-\u4e00\uac00-\ud7ff]+$/.test(value)){
                cb(new Error("只允许 数字 字母 _ 中/日/韩文"))
              }

              //不能和原来的一样
              if (value === this.userInfo.username){
                cb(new Error("用户名没有变化..."))
              }

              cb()
            }

          }
        ]
      }
    }
  },

  computed:{
    ...mapState(["userInfo"])
  },

  methods:{
    ...mapMutations(['loginSuccess']),
    submit(){
      this.$refs.userFrom.validate( async v => {
        if (v){
          //发起请求
          let {data} = await this.$axios({
            method:"POST",
            url:"/personal/name",
            data:{username:this.userFrom.username}
          })

          //修改失败
          if (data.code){
            return this.$message.error(data.message)
          }

        //修改成功
          this.$message.success("用户名修改成功")
          //更新vuex
          this.loginSuccess(data.data)
          //清空表单的内容
          this.$refs['userFrom'].resetFields();


        }else {
          return false
        }
      })
    },
  },

}
</script>

<style scoped lang="less">

.UpdateName {
  span.code{
    color: #bd4147;
    background-color: rgba(27,31,35,.05);
    font-weight: 700;
    font-size: 20px;
  }
}

</style>