<template>

  <div id="UpdatePass">
    <el-form
        :model="passwordFrom"
        status-icon
        :rules="rules"
        ref="passwordFrom"
        label-width="120px"
    >

      <el-form-item label="原密码：" prop="oldPass" label-width="120px">
        <el-input type="password" v-model="passwordFrom.oldPass" @input="validatePassword1" style="width:40%" placeholder="请输入你的原密码"></el-input>
      </el-form-item>
      <el-form-item label="新密码：" prop="password" label-width="120px">
        <el-input type="password" v-model="passwordFrom.password" @input="validatePassword2" style="width:40%" placeholder="请输入你的新密码"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码：" prop="password2" label-width="120px">
        <el-input type="password" v-model="passwordFrom.password2" style="width:40%" placeholder="请再次确认新密码"></el-input>
      </el-form-item>
      <el-form-item label-width="120px">
        <el-button type="primary" @click="submit">确认修改</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
import {mapMutations} from 'vuex';

export default {
  name: "ChangePassWord",
  data(){
    return{
      //表单绑定
      passwordFrom:{
        oldPass:"",
        password: "",
        password2: ""
      },
      //验证规则
      rules: {
        oldPass:[
          {
            validator(rule, value, cb) {
              //长度验证
              if ( value.length < 6 || value.length > 18) {
                cb(new Error("请输入6~18位的密码"))
              }
              //规则验证
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error("不允许的密码字符"))
              }

              cb()
            },
            trigger: "input"
          }
        ],
        password: [
          {
            validator(rule, value, cb) {
              //长度验证
              if (value.length < 6 || value.length > 18) {
                cb(new Error("请输入6~18位的密码"))
              }
              //规则验证
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error("不允许的密码字符"))
              }

              cb()
            },
            trigger: "input"
          }
        ],
        password2: [
          {
            validator: (rule, value, cb) => {
              if (this.passwordFrom.password !== value) {
                cb(new Error("两次输入密码不一致"))
              }

              if (value.length === 0){
              cb(new Error("没有输入密码") )
            }
              cb()
          },
          }
        ],
      }
    }
  },
  methods:{
    ...mapMutations(['loginOut']),
    validatePassword1(){
        this.$refs.passwordFrom.validateField('oldPass')
    },
    validatePassword2(){
      this.$refs.passwordFrom.validateField('password')
    },
    submit() {
      this.$refs.passwordFrom.validate( async v => {
        if (v) {

          let {data} = await this.$axios({
            method:"POST",
            url:"/personal/password",
            data:{
              OldPassWord:this.passwordFrom.oldPass,
              password:this.passwordFrom.password,
            }
          })

          //修改失败
          if (data.code){
            return this.$message.error(data.message)
          }

        //修改成功
          this.$message.success("修改成功，请重新登录")
          this.loginOut()//清除vuex
          this.$refs.passwordFrom.resetFields()//清空表单
          this.$router.push("/")//返回首页


        }else{
          return false
        }
      })
    }
  },
}
</script>

