<template>
  <div id="Admin">
    <article>
      <el-tabs v-model="activeName">
        <el-tab-pane label="发表文章" name="first">
          <AddArticle></AddArticle>
        </el-tab-pane>
        <el-tab-pane label="文章管理" name="second">
          <ArticleMessage></ArticleMessage>
        </el-tab-pane>
        <el-tab-pane label="添加友链" name="third">
          <LinkAdd></LinkAdd>
        </el-tab-pane>
        <el-tab-pane label="友链管理" name="fourth">
          <LinkMessage></LinkMessage>
        </el-tab-pane>
        <el-tab-pane label="反馈收集" name="fifth">
          <ContactMessage></ContactMessage>
        </el-tab-pane>
      </el-tabs>
    </article>
  </div>
</template>

<script>
import AddArticle from "../components/Admin/AddArticle";
import ArticleMessage from "../components/Admin/ArticleMessage";
import ContactMessage from "../components/Admin/ContactMessage";
import LinkAdd from "../components/Admin/LinkAdd";
import LinkMessage from "../components/Admin/LinkMessage";
import {mapState} from "vuex"

export default {
  name: "Admin",
  data(){
    return {
      activeName:"fifth"
    }
  },
  components:{
    AddArticle,ArticleMessage,ContactMessage,LinkAdd,LinkMessage
  },
  computed:{
    ...mapState(['userInfo'])
  },
  watch:{
    userInfo() {
      this.check()
    }
  },
  methods:{
    async check(){
      //验证账号权限
      let {data} = await this.$axios({
        method:"POST",
        url:"/adminServer/check"
      })

      if (data.code) {
        await this.$router.replace("/")
        this.$message.error("请以管理员身份登录")
      }

    }
  },
  async created() {
    await this.check()
  }

}
</script>

<style scoped lang="less">
#Admin {
  article {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: Quicksand, Microsoft YaHei, sans-serif;
    min-height: 721px;

    h2 {
      margin-bottom: 15px;
      font-size: 20px;
      letter-spacing: 3px;
      line-height: 24px;
      border-left: 5px solid #73b899;
      text-indent: 10px;
    }

    .el-tabs {
    }
  }
}
</style>