<template>
  <div id="User">
    <article>
      <h2>修改用户名</h2>
      <ChangeName></ChangeName>
    </article>
    <article>
      <h2>修改密码</h2>
      <ChangePassWord></ChangePassWord>
    </article>
    <article>
      <h2>修改头像</h2>
      <ChangePhoto></ChangePhoto>
    </article>
  </div>

</template>

<script>
import ChangeName from "../components/ChangeMessage/ChangeName";
import ChangePhoto from "../components/ChangeMessage/ChangePhoto";
import ChangePassWord from "../components/ChangeMessage/ChangePassWord";
import {mapState} from 'vuex'

export default {
  name: "ChangeUserMessage",
  components: {
    ChangeName,ChangePhoto,ChangePassWord
  },
  computed:{
    ...mapState(['userInfo'])
  },
  watch:{
    userInfo() {
      this.backHome()
    }
  },
  methods:{
    backHome(){
      if (!this.userInfo.username){
        this.$message.error("您还未登录，无法修改个人信息，请先登录后修改");
        this.$router.replace("/")
      }
    }
  },
  beforeRouteEnter(to,from,next){
    next(vm => {
      vm.backHome()
    })
  }
}
</script>

<style scoped lang="less">

article {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 15px;
  box-shadow: 0 0 4px #ddd;
  padding: 25px 20px;
  background-color: #fff;
  font-family: Quicksand, Microsoft YaHei, sans-serif;

  h2 {
    margin-bottom: 15px;
    font-size: 20px;
    letter-spacing: 3px;
    line-height: 24px;
    border-left: 5px solid #73b899;
    text-indent: 10px;
  }
}

</style>