<template>
  <div id="LinkManger">
    <el-table
        :data="linkData"
        border
        style="width: 100%"
    >
      <el-table-column
          label="名称">
        <template #default="scope">
          <el-input v-model="scope.row.name"></el-input>
        </template>
      </el-table-column>
      <el-table-column
          label="首页">
        <template #default="scope">
          <el-input v-model="scope.row.home"></el-input>
        </template>
      </el-table-column>
      <el-table-column
          label="logo">
        <template #default="scope">
          <el-input v-model="scope.row.logo"></el-input>
        </template>
      </el-table-column>
      <el-table-column
          label="描述">
        <template #default="scope">
          <el-input v-model="scope.row.des"></el-input>
        </template>
      </el-table-column>
      <el-table-column
          fixed="right"
          label="操作">
        <template #default="scope">
          <el-button type="primary" @click="update(scope.row)" size="small">修改</el-button>
          <el-button type="danger" @click="deleteLink(scope.row)" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {mapMutations, mapState} from "vuex";

export default {
  name: "LinkMessage",
  data(){
    return{}
  },
  computed:{
    ...mapState(['linkData'])
  },
  methods:{
    ...mapMutations(['updateLinkData']),
    //修改link
    async update(item){
      let {data} = await this.$axios({
        method:"POST",
        url:"/adminServer/link/update",
        data:{
          _id : item._id,
          name: item.name,
          home: item.home,
          logo: item.logo,
          des: item.des
        }
      })

      if (data.code) {return this.$message.error(data.code)}

      this.$message.success("更新完成")
    },

    //删除
    async deleteLink(item) {
      let {data} = await this.$axios({
        method: "delete",
        url: "/adminServer/link/delete",
        data: {
          _id: item._id
        }
      })

      if (data.code) {return this.$message.error(data.code)}

      this.$message.success("删除完成")
      this.getLinkData(this)
    },

    //获取
    async getLinkData(){
      let {data} =await this.$axios({
        method:"GET",
        url:"/get/link",
      })

      //如果失败
      if (data.code){
        return this.$message.error(data.message)
      }

      this.updateLinkData(data.data)
    }
  },
  created() {
    this.getLinkData(this)
  }

}
</script>

<style scoped lang="less">
#LinkManger {
  .el-button + .el-button {
    margin-left: 0;
  }
}

</style>